
.cartcontrol {
  font-size: 0;
}
.cartcontrol .decrease {
  width: 24px;
  height: 24px;
  display: inline-block;
  color: #00a0dc;
  opacity: 1;
  -webkit-transform: translate3D(0, 0, 0);
          transform: translate3D(0, 0, 0);
}
.cartcontrol .decrease .inner {
  display: inline-block;
  -webkit-transition: all 0.4s linear;
  transition: all 0.4s linear;
  -webkit-transform: rotate(0);
          transform: rotate(0);
  line-height: 24px;
  font-size: 24px;
}
.cartcontrol .decrease.move-enter-active,
.cartcontrol .decrease.move-leave-active {
  -webkit-transition: all 0.4s linear;
  transition: all 0.4s linear;
}
.cartcontrol .decrease.move-enter,
.cartcontrol .decrease.move-leave-active {
  opacity: 0;
  -webkit-transform: translate3D(24px, 0, 0);
          transform: translate3D(24px, 0, 0);
}
.cartcontrol .decrease.move-enter .inner,
.cartcontrol .decrease.move-leave-active .inner {
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}
.cartcontrol .add {
  width: 24px;
  height: 24px;
  display: inline-block;
  font-size: 24px;
  color: #00a0dc;
  line-height: 24px;
}
.cartcontrol .text {
  display: inline-block;
  width: 24px;
  text-align: center;
  font-size: 10px;
  color: #93999f;
  line-height: 24px;
  vertical-align: top;
}


