
.ratingselect .rating-type {
  padding: 18px 0;
  margin: 0 18px;
  border-bottom: 1px solid rgba(7,17,27,0.1);
}
.ratingselect .rating-type .wrapper {
  padding: 8px 12px;
  display: inline-block;
  margin-right: 8px;
}
.ratingselect .rating-type .wrapper.positive-wrapper {
  background: rgba(0,160,220,0.2);
  color: #4d555d;
}
.ratingselect .rating-type .wrapper.positive-wrapper.active {
  background: #00a0dc;
  color: #fff;
}
.ratingselect .rating-type .wrapper.negative-wrapper {
  background: rgba(77,85,93,0.2);
  color: #4d555d;
}
.ratingselect .rating-type .wrapper.negative-wrapper.active {
  background: #4d555d;
  color: #fff;
}
.ratingselect .rating-type .wrapper .rateType {
  font-size: 12px;
}
.ratingselect .rating-type .wrapper .price {
  font-size: 8px;
}
.ratingselect .switch {
  padding: 12px 18px;
  border-bottom: 1px solid rgba(7,17,27,0.1);
  color: #93999f;
  line-height: 24px;
}
.ratingselect .switch .icon-check_circle {
  font-size: 24px;
  vertical-align: top;
}
.ratingselect .switch .icon-check_circle.active {
  color: #00c850;
}
.ratingselect .switch .text {
  font-size: 12px;
  vertical-align: top;
}


