
.food {
  position: fixed;
  left: 0;
  top: 0;
  bottom: 46px;
  width: 100%;
  background: #fff;
  z-index: 30;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}
.food.appear-enter-active,
.food.appear-leave-active {
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}
.food.appear-enter,
.food.appear-leave-active {
  -webkit-transform: translate3d(100%, 0, 0);
          transform: translate3d(100%, 0, 0);
}
.food .image-header {
