  left: 0;
  height: 100%;
  width: 100%;
}
.food .image-header .back {
  position: absolute;
  top: 10px;
  left: 0;
}
.food .image-header .back .icon-arrow_lift {
  display: block;
  padding: 18px;
  font-size: 20px;
  color: #fff;
}
.food .detail-wrapper {
  padding: 18px;
}
.food .detail-wrapper .detail {
  position: relative;
}
.food .detail-wrapper .detail .title {
  font-size: 14px;
  font-weight: 700;
  color: #07111b;
  line-height: 14px;
  margin-bottom: 8px;
}
.food .detail-wrapper .detail .description {
